___CSS_LOADER_EXPORT___.push([module.id, `body {
  font-family: 'Roboto', sans-serif;
  background-color: bisque; 
  text-align: center;
}

h1 {
  font-weight: 700;
  color: #333;
  margin-top: 50px;
}

.button {
  display: inline-block;
  padding: 10px 20px;
  margin: 10px;
  background-color: white;
  color: black;
  text-decoration: none;
  border-radius: 5px;
  text-align: center;
  width: 200px;
  font-size: 18px;
}

.form-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80vh;
}

form {
  background-color: white;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  width: 400px;
  text-align: left;
}

label {
  display: block;
  margin-bottom: 10px;
  font-weight: bold;
}

input, textarea {
  width: 100%;
  padding: 10px;
  margin-bottom: 20px;
  border-radius: 5px;
  border: 1px solid #ccc;
  box-sizing: border-box; 
}

button.button {
  width: 100%;
  padding: 10px;
  background-color: rgb(88, 35, 5);
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
}

button.button:hover {
  background-color: rgb(100, 45, 20);
}

.back-button {
  position: fixed;
  top: 20px;
  left: 20px;
  width: 50px;
  height: 50px;
  background-color: rgb(88, 35, 5);
  color: white;
  font-size: 24px;
  font-weight: bold;
  border: none;
  border-radius: 10px;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.back-button:hover {
  background-color: rgb(100, 45, 20);
}

nav {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80vh;  
}

.news-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  margin-top: 20px;
}

.news-item {
  background-color: white;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  width: 250px;
  text-align: left;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.news-item p {
  margin: 5px 0;
  font-size: 14px;
  color: #333;
}

.news-item .title {
  font-weight: bold;
  color: #444;
}

.news-item .author {
  font-style: italic;
  color: #666;
}

.news-item .id {
  font-size: 12px;
  color: #888;
}

.form-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80vh;
  flex-direction: column;
  gap: 20px;
}

.news-item, .form-container form {
  margin: 10px;
  width: 100%;
  max-width: 400px;
}

@media (max-width: 768px) {
  .news-item, .form-container form {
    max-width: 100%;
  }
}
